/* Gallery container */
.gallery-container {
   text-align: center;
   padding: 20px;
   overflow-y: auto; /* Allow scrolling */
 }
 
 /* Grid layout: 3 rows, 2 columns */
 .gallery-grid {
   display: grid;
   grid-template-columns: repeat(2, 1fr); /* 2 columns */
   gap: 16px;
   justify-items: center;
 }
 
 /* Thumbnail container */
 .video-wrapper {
   position: relative;
   width: 150px;   /* Adjust size here */
   height: 100px;  /* Maintain aspect ratio */
   cursor: pointer;
   overflow: hidden;
   border-radius: 8px;
   box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
   transition: transform 0.2s ease;
 }
 
 .video-wrapper:hover {
   transform: scale(1.05);
 }
 
 /* Thumbnail image */
 .video-thumb {
   width: 100%;
   height: 100%;
   object-fit: cover;
   display: block;
   border-radius: 8px;
 }
 
 /* Play icon overlay */
 .play-overlay {
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   font-size: 24px;
   color: white;
   background-color: rgba(0, 0, 0, 0.5);
   padding: 6px 10px;
   border-radius: 50%;
   pointer-events: none;
 }
 
 /* Fullscreen overlay for video player */
 .video-overlay {
   position: fixed;
   top: 0;
   left: 0;
   z-index: 999;
   width: 100vw;
   height: 100vh;
   background-color: rgba(0, 0, 0, 0.85);
   display: flex;
   align-items: center;
   justify-content: center;
 }
 
 .overlay-video {
   width: 90vw;
   max-height: 90vh;
   border-radius: 12px;
 }
 